
*{
  margin: 0;
    padding: 0 ;
    box-sizing: border-box;
}
.App{

}
.cont{
  width: 1200px;
  margin: 0 auto;
}
.nav_wrap{
  display: flex;
  padding: 0;
  /* border: 1px solid #ccc; */
}
.nav_wrap li{
  width:calc(16.6666% - 11.6666px);
  height: 50px;
  line-height: 50px;
  border: 1px solid #ffa0c5;
  text-align: center;
  border-radius: 30px;
  margin-left: 10px;
  background-color: #ffa0c5;
  font-weight: bold;

}
.nav_wrap li a{
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 30px;
  transition: .3s;
  color: #fff;
}
.nav_wrap li:nth-child(6){
  margin-right: 10px;
}
